<template>
  <!-- 系统管理-用户管理 -->
  <div class="serach">
    <div class="login-out" title="退出" @click="logout"></div>
    <div class="table-pannel">
      <div class="btn-box">
        <div class="add-btn" @click="addDialog">
          <div class="add-btn-bg"></div>
          <div class="add-btn-name">新增</div>
        </div>
        <!-- <div class="edit-btn" @click="editDialog">
        <div class="edit-btn-bg"></div>
        <div class="edit-btn-name">修改</div>
      </div> -->
        <div class="del-btn" @click="delDialog">
          <div class="del-btn-bg"></div>
          <div class="del-btn-name">删除</div>
        </div>
        <div class="edit-btn" @click="roleTabShow = true">
          <div class="edit-btn-bg"></div>
          <div class="edit-btn-name">角色</div>
        </div>
      </div>
      <el-table
        stripe
        class="table"
        height="770"
        ref="multipleTable"
        :data="tableData"
        tooltip-effect="dark"
        style="width: 100%"
        @selection-change="handleSelectionChange"
      >
        <el-table-column type="selection" width="55" align="center">
        </el-table-column>
        <!-- <el-table-column prop="id" label="用户编号" width="80" align="center">
      </el-table-column> -->
        <el-table-column prop="urlName" label="权限名称" align="center">
        </el-table-column>
        <el-table-column prop="url" label="URL路径" align="center">
        </el-table-column>
        <el-table-column prop="open" label="是否公开" align="center">
          <template slot-scope="scope">
            <span v-if="scope.row.open == '0'"> 私有 </span>
            <span v-else> 公开 </span>
          </template>
        </el-table-column>
        <el-table-column prop="description" label="描述" align="center">
          <template slot-scope="scope">
            <span v-if="scope.row.description == null"> -- </span>
            <span v-else> {{ scope.row.description }}</span>
          </template>
        </el-table-column>
        <el-table-column prop="createTime" label="创建时间" align="center">
        </el-table-column>
        <el-table-column prop="name" label="操作" align="center">
          <template slot-scope="scope">
            <div class="table-operation">
              <div @click="handleEdit(scope.$index, scope.row)">
                <i class="el-icon-edit"></i>
                &nbsp;
                <span>修改</span>
              </div>
              <div @click="handleDel(scope.$index, scope.row)">
                <i class="el-icon-delete"></i>
                &nbsp;
                <span>删除</span>
              </div>
            </div>
          </template>
        </el-table-column>
      </el-table>
      <div class="pagination">
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page.sync="pageNum"
          background
          :page-size="pageSize"
          layout="total, prev, pager, next"
          :total="total"
        >
        </el-pagination>
      </div>
    </div>

    <!-- 添加 -->
    <el-dialog :title="title" :visible.sync="add" width="600px" append-to-body>
      <el-form ref="form" :model="addForm" :rules="addrules" label-width="80px">
        <el-row>
          <el-col :span="12">
            <el-form-item label="权限名称" prop="urlName">
              <el-input
                v-model="addForm.urlName"
                placeholder="请输入权限名称"
                clearable
              />
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="URL路径" prop="url">
              <el-input
                v-model="addForm.url"
                placeholder="请输入URL路径"
                clearable
              />
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="24">
            <el-form-item label="是否公开">
              <el-select
                v-model="addForm.open"
                placeholder="请选择是否公开"
                clearable
                style="width: 100%"
              >
                <!-- <el-option label="是" value="1"></el-option>
                <el-option label="否" value="0"></el-option> -->
                <el-option
                  v-for="item in dictOptions"
                  :key="item.id"
                  :label="item.dictName"
                  :value="item.dictVal"
                ></el-option>
              </el-select>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="24">
            <el-form-item label="角色选择">
              <el-select
                v-model="addForm.roleInfos"
                placeholder="请选择角色"
                multiple=""
                :disabled="addForm.open == '1'"
                clearable
                style="width: 100%"
              >
                <el-option
                  v-for="item in roleOptions"
                  :key="item.id"
                  :label="item.roleName"
                  :value="item.id"
                ></el-option>
              </el-select>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="24">
            <el-form-item label="描述">
              <el-input
                v-model="addForm.description"
                type="textarea"
                placeholder="请输入内容"
              ></el-input>
            </el-form-item>
          </el-col>
        </el-row>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button type="primary" @click="addSubmit">确 定</el-button>
        <el-button @click="addCancel">取 消</el-button>
      </div>
    </el-dialog>

    <!-- 修改 -->
    <el-dialog :title="title" :visible.sync="edit" width="600px" append-to-body>
      <el-form
        ref="form"
        :model="editForm"
        :rules="editrules"
        label-width="80px"
      >
        <el-row>
          <el-col :span="12">
            <el-form-item label="权限名称" prop="urlName">
              <el-input
                v-model="editForm.urlName"
                placeholder="请输入权限名称"
                clearable
              />
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="URL路径" prop="url">
              <el-input
                v-model="editForm.url"
                placeholder="请输入URL路径"
                clearable
              />
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="24">
            <el-form-item label="是否公开">
              <el-select
                v-model="editForm.open"
                placeholder="请选择是否公开"
                clearable
                style="width: 100%"
              >
                <el-option
                  v-for="item in dictOptions"
                  :key="item.id"
                  :label="item.dictName"
                  :value="item.dictVal"
                ></el-option>
              </el-select>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="24">
            <el-form-item label="角色选择">
              <el-select
                v-model="editForm.roleInfos"
                placeholder="请选择角色"
                multiple=""
                clearable
                style="width: 100%"
                :disabled="editForm.open == '1'"
              >
                <el-option
                  v-for="item in roleOptions"
                  :key="item.id"
                  :label="item.roleName"
                  :value="item.id"
                ></el-option>
              </el-select>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="24">
            <el-form-item label="描述">
              <el-input
                v-model="editForm.description"
                type="textarea"
                placeholder="请输入内容"
              ></el-input>
            </el-form-item>
          </el-col>
        </el-row>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button type="primary" @click="editSubmit">确 定</el-button>
        <el-button @click="editCancel">取 消</el-button>
      </div>
    </el-dialog>
    <div class="role-manage" v-show="roleTabShow">
      <div class="close-btn" @click="roleTabShow = false"></div>
      <!-- { leftOut: (activeRole.id = item.id) }, -->
      <ul>
        <li
          class="role-li"
          style="position: relative"
          v-for="(item, index) in roleOptions"
          :key="item.id"
          @click="editRoleFun($event, item)"
          @mousedown="createRipple($event, this)"
          @mouseup="removeRipple($event, this)"
        >
          <div
            :class="[
              'left-div',
              'leftIn',
              {
                active: index % 7 == 0,
                active1: index % 7 == 1,
                active2: index % 7 == 2,
                active3: index % 7 == 3,
                active4: index % 7 == 4,
                active5: index % 7 == 5,
                active6: index % 7 == 6,
                active7: index % 7 == 7,
              },
            ]"
          >
            <span></span>
          </div>
          <div class="font">{{ item.roleName }}</div>
        </li>
      </ul>
    </div>
    <el-dialog
      :title="title"
      :visible.sync="editRole"
      width="600px"
      append-to-body
    >
      <ul class="role-ul">
        <li
          v-for="item in tableData"
          :key="item.id.toString()"
          style="position: relative"
          :class="{
            active:
              editRoleForm.permissionInfos.indexOf(item.id.toString()) != -1,
          }"
          @click="changeRolePermiss(item)"
          @mousedown="createRipple($event, this)"
          @mouseup="removeRipple($event, this)"
          v-show="item.open == 0"
        >
          <span class="span-img"></span>
          <div class="font-desc">
            <div class="font-desc-name">{{ item.urlName }}</div>
            <div class="font-desc-url">{{ item.url }}</div>
          </div>
        </li>
      </ul>
      <!-- <el-form ref="form" :model="editRoleForm" label-width="80px">
        <el-row>
          <el-col :span="24">
            <el-form-item label="权限">
              <el-select
                v-model="editRoleForm.permissionInfos"
                placeholder="请选择权限"
                clearable
                style="width: 100%"
                multiple
              >
                <el-option
                  v-for="item in tableData"
                  :key="item.id.toString()"
                  :label="item.urlName"
                  :value="item.id.toString()"
                ></el-option>
              </el-select>
            </el-form-item>
          </el-col>
        </el-row>
      </el-form> -->
      <div slot="footer" class="dialog-footer">
        <el-button
          type="primary"
          @click="editRoleSubmit"
          style="background: #506ea1"
          >确 定</el-button
        >
        <el-button @click="editRole = false">取 消</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
export default {
  components: {
  },
  data () {
    return {
      roleTabShow: false,
      jsId: '',
      file: null,
      questionObject: {
        // questionsNmae: '',
        file: null
      },
      questionIds: [],
      formLabelWidth: '120px',
      fileList: [{ name: undefined }],
      templateShow: false,
      username: '',
      tel: '',
      dataStatus: '',
      pageSize: 15,
      pageNum: 0,
      total: 0,
      time: ['', ''],
      tableData: [],
      multipleSelection: [],
      switchStatus: '0',
      currentPage: 5,
      add: false,
      // 表单校验
      addrules: {
        urlName: [
          { required: true, message: "权限名称不能为空", trigger: "blur" }
        ],
        url: [
          { required: true, message: "URL路径不能为空", trigger: "blur" }
        ],
      },
      addForm: {
        urlName: '',
        url: '',
        open: '0',
        description: '',
        roleInfos: []
      },
      title: '',
      // 部门树选项
      deptOptions: undefined,
      // 性别状态字典
      //修改
      // 状态数据字典
      statusOptions: [],
      // 岗位选项
      postOptions: [],
      // 角色选项
      roleOptions: [],
      edit: false,
      editrules: {
        urlName: [
          { required: true, message: "权限名称不能为空", trigger: "blur" }
        ],
        url: [
          { required: true, message: "URL路径不能为空", trigger: "blur" }
        ],
      },
      editForm: {
        urlName: '',
        url: '',
        open: '0',
        description: '',
        roleInfos: [],
        id: ''
      },
      editRoleForm: {
        permissionInfos: []
      },

      resetFlag: false,
      resetForm: {
        password: ''
      },
      editRole: false,
      activeRole: {},
      dictOptions: []
    }
  },
  mounted () {
    this.searchFn()
    this.getRoles();
    this.queryDictByDictTypeId();
    // let dom = document.getElementsByClassName('role-li')[0];
    // dom.addEventListener('mousedown', this.createRipple)
    //document.addEventListener('mouseup', this.removeRipple)
    // this.getTreeselect()
    // this.handleSelectChange()
  },
  methods: {
    // changeOpen (val) {
    //   if (this.addForm.open == '0') {
    //     if (val == 'addForm') {
    //       this.addForm.roleInfos = []
    //     } else {
    //       this.editForm.roleInfos = []
    //     }
    //   }

    // },
    changeRolePermiss (item) {
      if (this.editRoleForm.permissionInfos.indexOf(item.id.toString()) == -1) {
        this.editRoleForm.permissionInfos.push(item.id.toString())
      } else {
        this.editRoleForm.permissionInfos.splice(this.editRoleForm.permissionInfos.indexOf(item.id.toString()), 1)
      }
    },
    logout () {
      this.$router.push('/')
    },
    queryDictByDictTypeId () {
      this.$get(this.$url.DATA_URL + '/queryDictByDictTypeId?dictTypeId=1').then((res) => {
        if (res.data.code == 0) {
          this.dictOptions = res.data.data;
        } else {
          this.$message({
            type: "error",
            message: res.data.msg
          });
        }
      })

    },
    queryPermissionByRoleId () {
      this.$get(this.$url.DATA_URL + '/queryPermissionsByRoleId?roleId=' + this.activeRole.id).then((res) => {
        if (res.data.code == 0) {
          this.editRoleForm.permissionInfos = [];
          for (let i = 0; i < res.data.data.length; i++) {
            this.editRoleForm.permissionInfos.push(res.data.data[i].id.toString())
          }
        } else {
          this.$message({
            type: "error",
            message: res.data.msg
          });
        }
      })
    },
    editRoleFun (e, item) {
      this.activeRole = item;
      this.editRole = true;
      this.queryPermissionByRoleId();
    },
    handleSelectionChange (val) {
      this.multipleSelection = val;
    },
    handleEdit (index, row) {
      this.title = '修改用户'
      this.edit = true
      this.editForm.urlName = row.urlName
      this.editForm.open = row.open.toString();
      this.editForm.url = row.url
      this.editForm.description = row.description
      this.editForm.roleInfos = [];
      for (let i = 0; i < row.roleInfos.length; i++) {
        this.editForm.roleInfos.push(row.roleInfos[i].id)
      }
      this.editForm.id = row.id
    },

    handleSizeChange (val) {
      this.pageSize = val
      // this.getUserList()
      this.searchFn()
    },
    handleCurrentChange (val) {
      this.pageNum = val
      // this.getUserList()
      this.searchFn()
    },
    handleSelectChange () {


    },
    addDialog () {
      this.addForm = {
        urlName: '',
        url: '',
        open: '0',
        description: '',
        roleInfos: []
      }
      this.title = '新增权限'
      this.add = true
      //this.getTreeselect()
    },
    getRoles () {
      this.$post(this.$url.DATA_URL + '/queryAllRoles').then((res) => {
        if (res.data.code == 0) {
          this.roleOptions = res.data.data
        } else {
          this.$message({
            type: "error",
            message: res.data.msg
          });
        }
      })
    },
    //新增确认按钮
    addSubmit () {
      this.$refs['form'].validate((valid) => {
        if (valid) {
          if (this.addForm.open == '0' && this.addForm.roleInfos.length == 0) {
            this.$message.warning('请选择角色!');
            return;
          }
          let params = {
            urlName: this.addForm.urlName,
            url: this.addForm.url,
            open: this.addForm.open,
            description: this.addForm.description,
            roleInfos: this.addForm.roleInfos
          }
          let roleArr = [];
          for (let i = 0; i < this.roleOptions.length; i++) {
            if (params.roleInfos.indexOf(this.roleOptions[i].id) != -1) {
              roleArr.push(this.roleOptions[i])
            }
          }
          params.roleInfos = roleArr;
          this.$post(this.$url.DATA_URL + '/addPermission', params).then((res) => {
            if (res.data.code == 0) {
              this.add = false;
              // this.getUserList();
              this.searchFn()
            } else {
              this.$message(
                {
                  type: 'error',
                  message: res.data.msg
                }
              )
            }
          })
        } else {
          this.add = true;
        }
      })

    },
    addCancel () {
      this.add = false
    },
    // 编辑弹窗
    editDialog () {
      console.log(this.multipleSelection, 'editDialog.multipleSelection')
      this.editForm = {
        urlName: '',
        url: '',
        open: '1',
        description: '',
        roleInfos: [],
        id: ''
      }
      if (this.multipleSelection.length > 1 || this.multipleSelection.length < 1) {
        return this.$message({
          type: 'error',
          message: "请选择一条权限信息",
        })
      } else {
        this.title = '修改权限'
        this.edit = true
        this.editForm.urlName = row.urlName
        this.editForm.open = row.open.toString();
        this.editForm.url = row.url
        this.editForm.description = row.description
        this.editForm.roleInfos = row.roleInfos
        this.editForm.id = row.id
      }
    },
    //修改确认按钮
    editSubmit () {
      let params = {
        urlName: this.editForm.urlName,
        open: this.editForm.open,
        url: this.editForm.url,
        description: this.editForm.description,
        roleInfos: this.editForm.roleInfos,
        id: this.editForm.id
      }
      let roleArr = [];
      for (let i = 0; i < this.roleOptions.length; i++) {
        if (params.roleInfos.indexOf(this.roleOptions[i].id) != -1) {
          roleArr.push(this.roleOptions[i])
        }
      }
      params.roleInfos = roleArr;
      this.$post(this.$url.DATA_URL + "/updatePermission", params).then((res) => {
        if (res.data.code == 0) {
          this.$message({
            type: "success",
            message: res.data.msg
          });
          this.searchFn()
        } else {
          this.$message({
            type: "error",
            message: res.data.msg
          });
        }
      })
      this.edit = false;
    },
    editRoleSubmit () {
      let params = {
        permissionInfos: [],
        id: this.activeRole.id
      }
      for (let i = 0; i < this.editRoleForm.permissionInfos.length; i++) {
        params.permissionInfos.push({ id: this.editRoleForm.permissionInfos[i] })
      }
      this.$post(this.$url.DATA_URL + "/bindPermissionByRole", params).then((res) => {
        if (res.data.code == 0) {
          this.$message({
            type: "success",
            message: res.data.msg
          });
          this.searchFn()
        } else {
          this.$message({
            type: "error",
            message: res.data.msg
          });
        }
      })
      this.editRole = false;
      this.activeRole = {};
    },
    editCancel () {
      this.edit = false
    },
    handleDel (index, row) {
      this.$confirm("此操作将永久删除该条权限信息, 是否继续?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
      }).then(() => {
        this.$get(this.$url.DATA_URL + `/delPermission?id=${row.id.toString()}`).then((res) => {
          if (res.data.code == 0) {
            this.$message({
              type: "success",
              message: '删除成功!'
            });
            this.searchFn()
          } else {
            this.$message({
              type: "error",
              message: res.data.msg
            });
          }
        })
      })
      // this.$delete(this.$url.DATA_URL + `/onlineteaching/user/${row.id}`).then((res) => {
      //   this.getUserList()
      // })
    },
    delDialog () {
      if (this.multipleSelection.length < 1) {
        return this.$message({
          type: 'error',
          message: "请选择一个用户",
        })
      }
      let that = this;
      this.$confirm("此操作将永久删除数据, 是否继续?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
      }).then(() => {
        for (let i = 0; i < that.multipleSelection.length; i++) {
          this.$get(this.$url.DATA_URL + `/delPermission?id=${that.multipleSelection[i].id.toString()}`).then((res) => {
            if (res.data.code == 0) {
              // this.getUserList()
              this.searchFn()
            } else {
              this.$message({
                type: "error",
                message: res.data.msg
              });
            }
          })
        }

      })


    },
    uploadFn () {
      this.templateShow = true
    },
    uploadChange (file) {
      this.questionObject.file = file.raw
    },
    uploadRemove () {
      this.file = null
    },
    // 转换
    formatter (params) {
      let paramsArr = params.split('&');
      let paramsStr = ''
      for (let item in paramsArr) {
        paramsStr += encodeURIComponent(paramsArr[item].split('=')[0]) + '='
        paramsStr += encodeURIComponent(paramsArr[item].split('=')[1]) + '&'
      }
      return paramsStr
    },

    searchFn () {
      if (!this.cookie.getCookie('username')) {
        this.$router.push('/login')
      }
      let params = {
        "urlName": "",
        "url": "",
        "permissionInfoPage": {
          "current": this.pageNum,
          "size": this.pageSize
        }
      }
      this.$post(this.$url.DATA_URL + "/queryPermission", params)
        .then((res) => {
          if (res.data.code === 0) {
            this.tableData = res.data.data.details;
            this.total = res.data.data.total;

          } else {
            this.$message({
              type: 'error',
              message: res.data.msg
            })
          }
        })
    },
    resetFn () {
      this.username = ''
      this.tel = ''
      this.dataStatus = ''
      this.time = []
      this.searchFn()
    },
    //按钮水波纹
    computeRippleStyles (element, event) {
      const { top, left } = element.getBoundingClientRect()
      const { clientWidth, clientHeight } = element

      const radius = Math.sqrt(clientWidth ** 2 + clientHeight ** 2) / 2
      const size = radius * 2

      const localX = event.clientX - left
      const localY = event.clientY - top

      const centerX = (clientWidth - radius * 2) / 2
      const centerY = (clientHeight - radius * 2) / 2

      const x = localX - radius
      const y = localY - radius

      return { x, y, centerX, centerY, size }
    },
    createRipple (event) {
      const container = event.currentTarget
      const { x, y, centerX, centerY, size } = this.computeRippleStyles(container, event)
      const ripple = document.createElement('div')
      ripple.classList.add('myripple')
      ripple.style.opacity = `0`
      ripple.style.transform = `translate(${x}px, ${y}px) scale3d(.3, .3, .3)`
      ripple.style.width = `${size}px`
      ripple.style.height = `${size}px`
      ripple.style.position = `absolute`
      ripple.style.top = `0`
      ripple.style.left = `0`
      ripple.style['z-index'] = `100`
      ripple.style['border-radius'] = `50%`
      ripple.style['background-color'] = '#fad4a1'//`currentColor`
      ripple.style['opacity'] = `0`
      ripple.style.transition = 'transform 0.2s cubic-bezier(0.68, 0.01, 0.62, 0.6),' +
        'opacity 0.08s linear';
      ripple.style['will-change'] = 'transform, opacity';
      ripple.style['pointer-events'] = 'none';
      // 记录水波的创建时间
      ripple.dataset.createdAt = String(performance.now())

      const { position } = window.getComputedStyle(container)
      container.style.overflow = 'hidden'
      position === 'static' && (this.style.position = 'relative')

      container.appendChild(ripple)

      window.setTimeout(() => {
        ripple.style.transform = `translate(${centerX}px, ${centerY}px) scale3d(1, 1, 1)`
        ripple.style.opacity = `.25`
      })
    },
    removeRipple (event) {
      const container = event.currentTarget
      const ripples = container.querySelectorAll('.myripple')
      if (!ripples.length) {
        return
      }

      const lastRipple = ripples[ripples.length - 1]
      // 通过水波的创建时间计算出扩散动画还需要执行多久，确保每一个水波都完整的执行了扩散动画
      const delay = 300 - performance.now() + Number(lastRipple.dataset.createdAt)

      setTimeout(() => {
        lastRipple.style.opacity = `0`

        setTimeout(() => lastRipple.parentNode?.removeChild(lastRipple), 300)
      }, delay)
    },

  }
}
</script>

