import Vue from 'vue'
import VueRouter from 'vue-router'
Vue.use(VueRouter)

import Login from '../pages/Login'
import User from '../pages/systemManagement/user.vue'
import cookie from '../utils/cookie'

 const router = new VueRouter({
    routes:[
      {
          path:'/',
          redirect:'/login'
      },
      {//登录
          path:'/login',
          component:Login
      },
      {//主页
        path:'/user',
        component:User
      }
    ]
})
export default router